<template>
  <div class="quiz">
    <button @click="changeHero"></button>
    <div v-if="hero">
      <dl>
        <div>
          <dt></dt>
          <dd>{{ this.hero.integrity }}</dd>
          <dt></dt>
          <dd>{{ this.hero.mercy }}</dd>
          <dt></dt>
          <dd>{{ this.hero.courage }}</dd>
        </div>
        <div>
          <dt></dt>
          <dd>{{ this.hero.strength }}</dd>
          <dt></dt>
          <dd>{{ this.hero.dexterity }}</dd>
          <dt></dt>
          <dd>{{ this.hero.wisdom }}</dd>
        </div>
        <div v-if="tips">
          <dt></dt>
          <dd>{{ this.hero.job }}</dd>
          <dt></dt>
          <dd>{{ this.hero.place }}</dd>
          <dt></dt>
          <dd>{{ this.hero.body }}</dd>
          <dt></dt>
          <dd>{{ this.hero.rudder ? "" : "" }}</dd>
        </div>
      </dl>
      <div>
        <input v-model="inputHero" autocomplete="on" list="kanas" />
        <datalist id="kanas">
          <option v-for="n in kanas" :key="n">{{n}}</option>
        </datalist>
        <button @click="check"></button>
        {{ message }}
      </div>
      <div>
        <button @click="viewTips"></button>
        <button @click="answer"></button>
        <div v-if="answerFlg">{{ this.hero.name }} {{ this.hero.kana }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import heros from "./../heros";

export default {
  name: "Quiz",
  data() {
    return {
      kanas: heros.heros.map(h => h.kana).sort(),
      hero: null,
      inputHero: "",
      message: "",
      answerFlg: false,
      tips: false,
      randCols: ["strength", "dexterity", "wisdom"]
    };
  },
  methods: {
    changeHero() {
      this.answerFlg = false;
      this.message = "";
      this.inputHero = "";
      this.tips = false;
      this.hero = heros.heros[Math.floor(Math.random() * heros.heros.length)];
    },
    check() {
      if (this.inputHero == this.hero.kana) {
        this.message = "";
        this.answerFlg = true;
      } else {
        this.message = "";
      }
    },
    answer() {
      this.answerFlg = true;
    },
    viewTips() {
      this.tips = true;
    }
  }
};
</script>

<style scoped>
dt {
  float: left;
  clear: left;
  margin-right: 0.5em;
  width: 120px;
}

dd {
  float: left;
  margin-left: 1em;
}
</style>
